import React from 'react'
import ReactDOM from 'react-dom'
import './index.css'
import bankOne from './bankOne';
import bankTwo from './bankTwo';
import 'bootstrap/dist/css/bootstrap.css';
import ToggleButton from 'react-toggle-button'
import MultiToggle from 'react-multi-toggle';

const groupOptions = [
    {
      displayName: '',
      value: true
    },
    {
      displayName: '',
      value: false
    },
  ];
class DrumMachine extends React.Component{
    constructor(props){
        super(props);
        this.State = {
            Bank:true,
            Power:true,
            Caption : ''
        }
        this.playSound = this.playSound.bind(this);
        this.toggleBank = this.toggleBank.bind(this);
        this.togglePower = this.togglePower.bind(this);
        this.updatingItem = this.updatingItem.bind(this);
        this.handleKeyPress = this.handleKeyPress.bind(this);
    }
    componentDidMount() {
   //     const script = document.createElement("script");
    
   //     script.src =
   //       "https://cdn.freecodecamp.org/testable-projects-fcc/v1/bundle.js";
   //     script.async = true;
    
   //     document.body.appendChild(script);
        document.addEventListener('keydown', this.handleKeyPress);
      }
      componentWillUnmount() {
        document.removeEventListener('keydown', this.handleKeyPress);
      }
    toggleBank(){
        this.State.Bank = this.State.Bank ? false:true              
        this.updatingBankState();     
    }    
    togglePower(){
        this.State.Power = this.State.Power ? false:true;
        this.updatingPowerState();
    }
    handleKeyPress(e) {
        e.preventDefault()
        if(!this.State.Power){return;}
        const sound = document.getElementById(e.key.toUpperCase());
        if(sound === null){return;}
        this.audioPlay(sound);
      }
    playSound(e){
        e.preventDefault()
        if(!this.State.Power){return;}
        const sound = e.currentTarget;
        this.audioPlay(sound);      
    }
    audioPlay(audioRecord){
        let audio = new Audio(audioRecord.querySelector('.clip').src);
        audio.play();
        let index = bankOne.findIndex(item => item.keyTrigger === audioRecord.id)
        let arrayItemOne = bankOne[index];
        let arrayItemTwo = bankTwo[index];
        var displayID = this.State.Bank ? arrayItemOne.id : arrayItemTwo.id;
        this.State.Caption = displayID;
        this.updatingItem();
    }
    updatingItem(){
        this.setState({
            Caption:this.State.Caption});
    }
    updatingBankState(){
        this.setState({
            Bank:this.State.Bank});
    }
    updatingPowerState(){
        this.setState({
            Power:this.State.Power});
    }
    
    render(){
        const drumBuilderOne = Array.from(bankOne).map(item => {
            return (
                <div className='drum-pad' id={item.keyTrigger} key={item.keyTrigger} onClick={this.playSound} >
                    {item.keyTrigger}
                    <audio src={item.url} className='clip' id={item.keyTrigger} key={item.keyTigger} name={item.id} />
                </div>
            );
        });
        const drumBuilderTwo = Array.from(bankTwo).map(item => {
            return (
                <div className='drum-pad' id={item.keyTrigger} key={item.keyTrigger} onClick={this.playSound} >
                    {item.keyTrigger}
                    <audio src={item.url} className='clip' id={item.keyTrigger} key={item.keyTrigger} name={item.id}/>
                </div>
            );
        });
        return(
            <div id="drum-machine">
                    <div id="drum-container">
                        {this.State.Bank ? drumBuilderOne : drumBuilderTwo}
                    </div>                   
                    <div id="option-container">
                    <p id="display">{this.State.Caption}</p>
                        <div id="display">
                            <p>Bank</p>
                            <MultiToggle
                                options={groupOptions}
                                selectedOption={this.State.Bank}
                                onSelectOption={this.toggleBank}/>
                        </div>
                        <div id="display"><p>Power</p>
                        <ToggleButton
                            value={ this.State.Power}
                            onToggle={this.togglePower} />
                        </div>                        
                    </div>                                     
            </div>    
        )
    }
}
class MainApp extends React.Component{
    render(){
        return(
            <div>
                <DrumMachine />
            </div>
        )
    }
}
//===========
ReactDOM.render(
    <MainApp />,
    document.getElementById('root')
);
